"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import Image from "next/image"
import { jwtDecode } from "jwt-decode"
import {
  LayoutDashboard,
  ClipboardList,
  CheckCircle,
  LogOut,
  Menu,
  Users,
  Network,
  UserCog,
  ChevronDown,
  ChevronRight,
} from "lucide-react"
import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"

interface DecodedToken {
  role: string
  user_id?: string
  userId?: string
}

const Sidebar = () => {
  const [isCoordinationOpen, setCoordinationOpen] = useState(false)
  const [isRepartitionOpen, setRepartitionOpen] = useState(false)
  const [isSidebarOpen, setSidebarOpen] = useState(true)
  const [userRole, setUserRole] = useState("")
  const [userId, setUserId] = useState("")

  useEffect(() => {
    const getUserInfo = () => {
      // Using optional chaining to safely access cookies
      const token =
        typeof document !== "undefined"
          ? document.cookie
              .split("; ")
              .find((row) => row.startsWith("token="))
              ?.split("=")[1]
          : null

      if (token) {
        try {
          const decoded = jwtDecode<DecodedToken>(token)
          setUserRole(decoded.role)
          if (decoded.user_id) {
            setUserId(decoded.user_id)
          } else if (decoded.userId) {
            setUserId(decoded.userId)
          }
        } catch (error) {
          console.error("Erreur lors du décodage du token:", error)
        }
      }
    }
    getUserInfo()
  }, [])

  const toggleCoordination = () => setCoordinationOpen(!isCoordinationOpen)
  const toggleRepartition = () => setRepartitionOpen(!isRepartitionOpen)
  const toggleSidebar = () => setSidebarOpen(!isSidebarOpen)

  const isCoordinationAllowed = userRole === "responsableFiliere"

  return (
    <div
      className={cn(
        "h-screen bg-white text-black p-4 transition-all duration-500 shadow-lg flex flex-col z-30",
        isSidebarOpen ? "w-80" : "w-20",
      )}
    >
      <div className="flex items-center justify-between mb-1">
        <div className="flex items-center">
          {isSidebarOpen ? (
            <Image
              src="/ESTSLOGO24.png"
              alt="School Logo"
              width={300}
              height={150}
              className="object-contain"
              style={{ marginTop: "-60px" }}
            />
          ) : (
            <Image src="/ESTSLOGO24.png" alt="School Logo" width={60} height={60} className="object-contain" />
          )}
        </div>
        <Button variant="ghost" size="icon" className="md:hidden" onClick={toggleSidebar} aria-label="Toggle Sidebar">
          <Menu className="h-5 w-5" />
        </Button>
      </div>

      <ul className="space-y-2 mt-4">
        <li>
          <Link
            href="/dashboard"
            className={cn(
              "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
              !isSidebarOpen && "justify-center",
            )}
          >
            <LayoutDashboard className="h-5 w-5" />
            {isSidebarOpen && <span>Dashboard</span>}
          </Link>
        </li>

        {isCoordinationAllowed && (
          <>
            <li
              className={cn(
                "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
                !isSidebarOpen && "justify-center",
              )}
              onClick={toggleCoordination}
            >
              <ClipboardList className="h-5 w-5" />
              {isSidebarOpen && (
                <div className="flex items-center justify-between w-full">
                  <span>Coordination</span>
                  {isCoordinationOpen ? <ChevronDown className="h-4 w-4" /> : <ChevronRight className="h-4 w-4" />}
                </div>
              )}
            </li>

            {isCoordinationOpen && isSidebarOpen && (
              <ul className="ml-5 space-y-2">
                <li
                  className={cn(
                    "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
                  )}
                  onClick={toggleRepartition}
                >
                  <Network className="h-5 w-5" />
                  <div className="flex items-center justify-between w-full">
                    <span>Répartition</span>
                    {isRepartitionOpen ? <ChevronDown className="h-4 w-4" /> : <ChevronRight className="h-4 w-4" />}
                  </div>
                </li>

                {isRepartitionOpen && (
                  <ul className="ml-5 space-y-2">
                    <li>
                      <Link
                        href="/users/readEtu"
                        className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                      >
                        <Users className="h-5 w-5" />
                        <span>Gestion Étudiants</span>
                      </Link>
                    </li>
                    <li>
                      <Link
                        href="/users/readEnca"
                        className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                      >
                        <Users className="h-5 w-5" />
                        <span>Gestion Encadrants</span>
                      </Link>
                    </li>
                    <li>
                      <Link
                        href="/users/readTute"
                        className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                      >
                        <Users className="h-5 w-5" />
                        <span>Gestion Tuteur</span>
                      </Link>
                    </li>
                    <li>
                      <Link
                        href="/users/readSuj"
                        className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                      >
                        <ClipboardList className="h-5 w-5" />
                        <span>Gestion Sujet</span>
                      </Link>
                    </li>
                    <li>
                      <Link
                        href="/users/readgenerer"
                        className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                      >
                        <Users className="h-5 w-5" />
                        <span>Gestion des groupes</span>
                      </Link>
                    </li>
                  </ul>
                )}

                <li>
                  <Link
                    href="/users/readaffec"
                    className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                  >
                    <Users className="h-5 w-5" />
                    <span>Affectation</span>
                  </Link>
                </li>

                <li>
                  <Link
                    href="/users/compte"
                    className="flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300"
                  >
                    <UserCog className="h-5 w-5" />
                    <span>Gestion des comptes</span>
                  </Link>
                </li>
              </ul>
            )}
          </>
        )}

        {userRole === "tuteur" && userId && (
          <li>
            <Link
              href={`/users/Tutinterface/${userId}`}
              className={cn(
                "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
                !isSidebarOpen && "justify-center",
              )}
            >
              <CheckCircle className="h-5 w-5" />
              {isSidebarOpen && <span>Consultation</span>}
            </Link>
          </li>
        )}

        {userRole === "encadrant" && userId && (
          <li>
            <Link
              href={`/users/EncadrantInterface/${userId}`}
              className={cn(
                "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
                !isSidebarOpen && "justify-center",
              )}
            >
              <CheckCircle className="h-5 w-5" />
              {isSidebarOpen && <span>Consultation</span>}
            </Link>
          </li>
        )}

        {userRole === "etudiant" && userId && (
          <li>
            <Link
              href={`/users/Etinterface/${userId}`}
              className={cn(
                "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
                !isSidebarOpen && "justify-center",
              )}
            >
              <CheckCircle className="h-5 w-5" />
              {isSidebarOpen && <span>Consultation</span>}
            </Link>
          </li>
        )}

        <li
          className={cn(
            "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
            !isSidebarOpen && "justify-center",
          )}
        >
          <CheckCircle className="h-5 w-5" />
          {isSidebarOpen && <span>Suivi</span>}
        </li>

        <li
          className={cn(
            "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
            !isSidebarOpen && "justify-center",
          )}
        >
          <CheckCircle className="h-5 w-5" />
          {isSidebarOpen && <span>Soutenance</span>}
        </li>

        <li>
          <Link
            href="/"
            className={cn(
              "flex items-center gap-3 p-3 hover:bg-[#D2B48C] rounded-md cursor-pointer transition-all duration-300",
              !isSidebarOpen && "justify-center",
            )}
          >
            <LogOut className="h-5 w-5" />
            {isSidebarOpen && <span>Déconnexion</span>}
          </Link>
        </li>
      </ul>
    </div>
  )
}

export default Sidebar

