"use client"

import Link from "next/link"
import { useState, useEffect } from "react"
import { Bell, Mail, User } from "lucide-react"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"

interface Notification {
  id: number
  title: string
  description: string
  date: string
}

interface Message {
  id: number
  from: string
  text: string
}

const Navbar = () => {
  const [notifCount, setNotifCount] = useState(0)
  const [msgCount, setMsgCount] = useState(0)
  const [notifications, setNotifications] = useState<Notification[]>([
    { id: 1, title: "Nouveau projet assigné", description: "Vous avez été assigné au projet X.", date: "2025-02-13" },
    { id: 2, title: "Mise à jour de statut", description: "Le projet Y a été validé.", date: "2025-02-12" },
    {
      id: 3,
      title: "Date limite approchante",
      description: "La date limite de soumission pour le rapport est le 20 février.",
      date: "2025-02-10",
    },
  ])
  const [messages, setMessages] = useState<Message[]>([
    { id: 1, from: "Prof. Ahmed", text: "Merci pour votre rapport." },
    { id: 2, from: "Administration", text: "Votre compte a été mis à jour." },
  ])

  useEffect(() => {
    setNotifCount(notifications.length)
    setMsgCount(messages.length)
  }, [notifications, messages])

  return (
    <header className="flex justify-between items-center p-4 lg:p-6 bg-white text-gray-800 shadow-lg w-full">
      <Link href="/" className="text-2xl font-bold hover:text-gray-500 transition">
        {/* Logo can go here if needed */}
      </Link>

      <div className="flex items-center gap-4 lg:gap-6">
        <TooltipProvider>
          <Tooltip>
            <TooltipTrigger asChild>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon" className="relative">
                    <Bell className="h-5 w-5" />
                    {notifCount > 0 && (
                      <Badge className="absolute -top-1 -right-1 h-5 w-5 flex items-center justify-center p-0 bg-red-500 text-white">
                        {notifCount}
                      </Badge>
                    )}
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end" className="w-80">
                  {notifications.length === 0 ? (
                    <DropdownMenuItem>Aucune notification</DropdownMenuItem>
                  ) : (
                    notifications.map((notif) => (
                      <DropdownMenuItem key={notif.id} className="flex flex-col items-start p-3">
                        <span className="font-bold">{notif.title}</span>
                        <span className="text-sm">{notif.description}</span>
                        <span className="text-xs text-gray-500 mt-1">{notif.date}</span>
                      </DropdownMenuItem>
                    ))
                  )}
                </DropdownMenuContent>
              </DropdownMenu>
            </TooltipTrigger>
            <TooltipContent>Notifications</TooltipContent>
          </Tooltip>
        </TooltipProvider>

        <TooltipProvider>
          <Tooltip>
            <TooltipTrigger asChild>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon" className="relative">
                    <Mail className="h-5 w-5" />
                    {msgCount > 0 && (
                      <Badge className="absolute -top-1 -right-1 h-5 w-5 flex items-center justify-center p-0 bg-blue-500 text-white">
                        {msgCount}
                      </Badge>
                    )}
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end" className="w-80">
                  {messages.length === 0 ? (
                    <DropdownMenuItem>Aucun message</DropdownMenuItem>
                  ) : (
                    messages.map((msg) => (
                      <DropdownMenuItem key={msg.id} className="flex flex-col items-start p-3">
                        <span className="font-bold">{msg.from}:</span>
                        <span className="text-sm">{msg.text}</span>
                      </DropdownMenuItem>
                    ))
                  )}
                </DropdownMenuContent>
              </DropdownMenu>
            </TooltipTrigger>
            <TooltipContent>Messages</TooltipContent>
          </Tooltip>
        </TooltipProvider>

        <TooltipProvider>
          <Tooltip>
            <TooltipTrigger asChild>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="icon">
                    <User className="h-5 w-5" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem>Mon Profil</DropdownMenuItem>
                  <DropdownMenuItem>Paramètres</DropdownMenuItem>
                  <DropdownMenuItem>Déconnexion</DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </TooltipTrigger>
            <TooltipContent>Compte</TooltipContent>
          </Tooltip>
        </TooltipProvider>
      </div>
    </header>
  )
}

export default Navbar

